.spot {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.svg-wrapper {
  margin-top: 40px;
  position: relative;
  width: 150px;
  /*make sure to use same height/width as in the html*/
  height: 40px;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
  overflow: hidden;
  cursor: pointer;
}

.shape {
  display: block;
  stroke-width: 4px;
  fill: transparent;
  stroke: #ccc;
  stroke-dasharray: 85 400;
  stroke-dashoffset: -220;
  transition: 1.5s all ease;
}

.text {
  display: block;
  margin-top: -35px;
  text-align: center;
  a {
    color: $colorDarkGrey;
    text-decoration: none;
    text-rendering: optimizeLegibility;
    font-family: "Roboto", Arial, sans-serif;
    font-size: 14px;
    line-height: 1;
  }
}

.svg-wrapper:hover .shape {
  stroke-dasharray: 50 0;
  stroke-width: 3px;
  stroke-dashoffset: 0;
  stroke: #e3b673;
}

.active .shape {
  stroke-dasharray: 50 0;
  stroke-width: 3px;
  stroke-dashoffset: 0;
  stroke: #ffefff;
}
