#page-home {
  text-align: center;
  height: 100%;
  color: #e9ece5;
  background-color: #000000;
  .brand-logo {
    #brand-logo {
      width: 210px;
      border: solid #e3b673 5px;
      * {
        position: relative;
        fill: #e3b673;
        transform-origin: center top;
        transition: all 0.4s ease;
      }
    }
    padding: 65px 0 80px 0;
    #circle {
      height: 240px;
    }
  }
  .h-01 {
    color: #e3b673;
    margin-bottom: 50px;
  }
  .copy-02 {
    color: #e3b673;
    margin-bottom: 180px;
    padding-top: 90px;
    text-shadow: -1px 0 rgba(0, 0, 0, 0.31), 0 1px rgba(0, 0, 0, 0.31), 1px 0 rgba(0, 0, 0, 0.31),
      0 -1px rgba(0, 0, 0, 0.31);
  }
}
